import React from "react";
import "./Retirement.css";
import "../Home/Home.css";
import RetirementMain from "../Home/images/Retirement/1.png";
import { withTranslation } from "react-i18next";

export class Retirement extends React.Component {
  render() {
    const { t } = this.props;
    return (
      <section>
        <div id="Retirement-Banner">
          <div className="container container-wrapper">
            <div className="row">
              <div className="col-lg-7 col-md-6 col-12 reitred-box">
                <h2>{t("RetirementPlan:RP1")}</h2>
                <p>{t("RetirementPlan:RP2")}</p>
                <a
                  href="https://app.nivesh.com/home/index"
                  className="btn btn-primary"
                  target="_blank"
                >
                  {t('CTAFooter:InvestNow1')}
                </a>
              </div>

              <div className="col-lg-5 col-md-6 col-12">
                <img src={RetirementMain} alt="" className="img-fluid" />
              </div>
            </div>
          </div>
        </div>

        <div id="arncode-section">
          <div className="container container-wrapper">
            <div className="row ">
              <h2>{t("RetirementPlan:RP3")}</h2>
              <ul>
                <li>{t("RetirementPlan:RP4")}</li>
                <li>{t("RetirementPlan:RP5")}</li>

                <li>
                  Most of the schemes levy an exit load on redemptions before a
                  particular age.
                </li>
                <li>{t("RetirementPlan:RP6")}</li>
              </ul>
            </div>
          </div>
        </div>

        <div id="retired-thirdsection">
          <div className="container container-wrapper">
            <div className="row">
              <h2>{t("RetirementPlan:RP5")}</h2>
              <div className="col-lg-12 table-head">
                <h3>1) Reliance Retirement Fund</h3>
                <div className="table-responsive">
                  <table className="table custom-table">
                    <colgroup>
                      <col className="col-lg-5 col-sm-4" />
                      <col className="col-lg-4 col-sm-4" />
                      <col className="col-lg-3 col-sm-4" />
                    </colgroup>
                    <thead className="thead-blue">
                      <tr>
                        <th>Features </th>
                        <th>Exit Load </th>
                        <th>Lock-in Period</th>
                      </tr>
                    </thead>
                    <tbody>
                      <tr>
                        <th>
                          2 options:
                          <ul>
                            <li> Wealth Creation Plan (65%-100% Equities)</li>
                            <li> Income Generation Plan (0%-35% Equities).</li>
                          </ul>
                        </th>
                        <td>1% up to age of 60</td>
                        <td>5 years</td>
                      </tr>
                    </tbody>
                  </table>
                </div>
              </div>

              <div className=" col-lg-12 table-head">
                <h3>2) HDFC Retirement Savings Fund</h3>
                <div className="table-responsive">
                  <table className="table custom-table">
                    <colgroup>
                      <col className="col-lg-5 col-sm-4" />
                      <col className="col-lg-4 col-sm-4" />
                      <col className="col-lg-3 col-sm-4" />
                    </colgroup>
                    <thead className="thead-blue">
                      <tr>
                        <th scope="col">Features</th>
                        <th scope="col">Exit Load</th>
                        <th scope="col">Lock-in Period</th>
                      </tr>
                    </thead>
                    <tbody>
                      <tr>
                        <th scope="col">
                          3 options:
                          <ul>
                            <li> Equity Plan (80%-100% Equities),</li>
                            <li>Hybrid Debt Plan (5%-30% Equities)</li>
                            <li> Hybrid Equity Plan (60%-80% Equities), and</li>
                          </ul>
                        </th>
                        <td scope="col">1% Up to age of 60</td>
                        <td scope="col">5 years</td>
                      </tr>
                    </tbody>
                  </table>
                </div>
              </div>

              <div className="col-lg-12 table-head">
                <h3>3) Franklin India Pension Fund</h3>
                <div className="table-responsive">
                  <table className="table custom-table">
                    <colgroup>
                      <col className="col-lg-5 col-sm-4" />
                      <col className="col-lg-4 col-sm-4" />
                      <col className="col-lg-3 col-sm-4" />
                    </colgroup>
                    <thead className="thead-blue">
                      <tr>
                        <th scope="col">Features </th>
                        <th scope="col">Exit Load </th>
                        <th scope="col">Lock-in Period</th>
                      </tr>
                    </thead>
                    <tbody>
                      <tr>
                        <th scope="col">
                          1 options:
                          <ul>
                            <li>Up to 40% in Equities</li>
                          </ul>
                        </th>
                        <td>3% up to age of 58</td>
                        <td>3 years</td>
                      </tr>
                    </tbody>
                  </table>
                </div>
              </div>

              <div className="col-lg-12 table-head">
                <h3>4) UTI Retirement Benefit Pension Fund</h3>
                <div className="table-responsive">
                  <table className="table custom-table">
                    <colgroup>
                      <col className="col-lg-5 col-sm-4" />
                      <col className="col-lg-4 col-sm-4" />
                      <col className="col-lg-3 col-sm-4" />
                    </colgroup>
                    <thead className="thead-blue">
                      <tr>
                        <th scope="col">Features</th>
                        <th scope="col">Exit Load</th>
                        <th scope="col">Lock-in Period</th>
                      </tr>
                    </thead>
                    <tbody>
                      <tr>
                        <th scope="row">
                          1 options:
                          <ul>
                            <li>Up to 40% in Equities</li>
                          </ul>
                        </th>
                        <td>
                          <ul>
                            <li>5% up to 1 year,</li>
                            <li>3% for 1-3 years,</li>
                            <li>1% on more than 3 years and up to age of 58</li>
                          </ul>
                        </td>
                        <td>Nil</td>
                      </tr>
                    </tbody>
                  </table>
                </div>
              </div>
            </div>
          </div>
        </div>

        <div id="arncode-section">
          <div className="container container-wrapper">
            <div className="row ">
              <h2>{t("RetirementPlan:RP8")}</h2>
              <ul>
                <li>{t("RetirementPlan:RP9")}</li>
                <li>{t("RetirementPlan:RP10")}</li>
                <li>{t("RetirementPlan:RP11")}</li>
              </ul>
            </div>
          </div>
        </div>

        <div id="Retirement-schemeSection">
          <div className="container container-wrapper">
            <h2>{t("RetirementPlan:RP12")}</h2>
            <div className="row">
              <table className="table custom-table">
                <thead className="thead-blue">
                  <tr>
                    <th scope="col"> </th>
                    <th scope="col">{t("RetirementPlan:RP13")} </th>
                    <th scope="col">{t("RetirementPlan:RP14")} </th>
                    <th scope="col">{t("RetirementPlan:RP15")}</th>
                  </tr>
                </thead>
                <tbody>
                  <tr>
                    <th scope="row">{t("RetirementPlan:RP16")}</th>
                    <td>{t("RetirementPlan:RP17")}</td>
                    <td>{t("RetirementPlan:RP18")}</td>
                    <td>{t("RetirementPlan:RP19")}</td>
                  </tr>
                  <tr>
                    <th scope="row">{t("RetirementPlan:RP20")}</th>
                    <td>{t("RetirementPlan:RP21")}</td>
                    <td>{t("RetirementPlan:RP22")}</td>
                    <td>{t("RetirementPlan:RP23")}</td>
                  </tr>
                </tbody>
              </table>
            </div>
            <div className="text-center">
              <a
                href="https://app.nivesh.com/Home/Index?GoalCatId=3"
                className="btn btn-primary"
                target="_blank"
              >
                {t('CTAFooter:InvestNow1')}
              </a>
            </div>
          </div>
        </div>
      </section>
    );
  }
}

export default withTranslation()(Retirement);
